import { useState, useEffect } from 'react';

import Header from '../../components/Header/Header.jsx'
import AuthInput from '../../components/Auth/AuthInput/AuthInput.jsx'
import Button from '../../components/Button/Button.jsx'
import NavBarContainer from '../../components/Navbar/NavBarContainer/NavBarContainer.jsx'
import { setUserAccount, getUser } from '../../api/user.js'
import { Toast } from '../../utility/helper.js'
import styles from './SettingPage.module.scss'
import { useAuth } from '../../context/AuthContext.jsx'
import { useNavigate } from 'react-router-dom';
import { useDataStatus } from '../../context/DataContext.jsx'

export default function SettingPage () {
  const { currentUser, isAuthenticated } = useAuth();

  const userId = currentUser.id

  const navigate = useNavigate();
  const [account, setAccount] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [checkPassword, setCheckPassword] = useState("");
  const { isDataUpdate, setIsDataUpdate } = useDataStatus();
  
  useEffect(() => {
        const getUserInfo = async () => {
            try {
                const data = await getUser(userId);
                if (data.status === 'error') {
                    console.log(data.message);
                    return;
                }
                if (data) {
                    // update data
                    setAccount(data.account);
                    setName(data.name)
                    setEmail(data.email)
                    console.log(data);
                }
            } catch (error) {
                console.log('', error);
            }
        };
        getUserInfo();
    }, [userId, isDataUpdate]);

  const handleClick = async () => {
    if (account.trim().length === 0) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    if (name.trim().length === 0) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    if (email.trim().length === 0) {
      Toast.fire({
        title: "email!",
        icon: "error",
      });
      return;
    }
    if (checkPassword !== password) {
      Toast.fire({
        title: "!",
        icon: "error",
      });
      return;
    }
    if (!email.includes("@")) {
      Toast.fire({
        title: "email!",
        icon: "error",
      });
      return;
    }
    const data = await setUserAccount({
      name,
      account,
      email,
      password,
      checkPassword,
      userId
    });
   
    if (data.status==="error") {
      Toast.fire({
        title: data.message,
        icon: "error",
      });
      return;
    } 
    Toast.fire({
        title: "",
        icon: "success",
      });
    setIsDataUpdate(!isDataUpdate)
      // localStorage.removeItem('token')
      // navigate('/login');
  }

    useEffect(() => {
        if (!isAuthenticated) {
        navigate('/login');
        }
    }, [navigate, isAuthenticated]);


  return (
    <div className={styles.adminContainer}>
      <NavBarContainer role='user'page='setting'/>
      <div className={styles.middleContainer}>
        <Header title=''/>
        <div className={styles.inputContainer}>
          {account &&<AuthInput label='' value={account} placeholder='' onChange={(accountInputValue) => setAccount(accountInputValue)}
          notification='!' wordsLimit={20}/>}
          {name && <AuthInput label='' value={name} placeholder='' onChange={(nameInputValue) => setName(nameInputValue)}
          notification='!' wordsLimit={20}/>}
          {email && <AuthInput label='Email' value={email} placeholder='Email' onChange={(emailInputValue) => setEmail(emailInputValue)}
          notification='!' wordsLimit={20}/>}
          
          <AuthInput label='' type='password' value={password} placeholder='' onChange={(passwordInputValue) => setPassword(passwordInputValue)}
          notification='!' wordsLimit={20}
          />
          <AuthInput label='' type='password' value={checkPassword} placeholder='' onChange={(checkPasswordInputValue) => setCheckPassword(checkPasswordInputValue)}
          notification='!' wordsLimit={20}
          />
          <Button title='' size='small' isAction onClick={handleClick}></Button>
        </div>
      </div>
      <div className={styles.placeHolder}></div>
    </div>
  )
}